body {
  font-family: 15px/1.5 'Spoqa Han Sans',"Roboto","Apple SD Gothic Neo","Malgun Gothic","Arial,sans-serif,serif";
  padding: 0;
  margin: 0;
  background-color: #fff; }

.container {
  width: 80%;
  margin: auto;
  overflow: hidden; }

.ul {
  margin: 0;
  padding: 0; }

.header {
  background: #fff;
  padding-top: 10px;
  min-height: 50px;
  z-index: 0;
  -webkit-box-shadow: rgba(0, 0, 0, 0.117647) 0 1px 3px;
          box-shadow: rgba(0, 0, 0, 0.117647) 0 1px 3px; }
  .header .context {
    color: #999;
    text-decoration: none;
    text-transform: uppercase;
    font-size: 16px; }
  .header nav {
    float: right;
    margin-top: 10px; }
  .header li {
    float: left;
    display: inline;
    padding: 10px 15px 20px 0px; }

.highlight :hover {
  color: #169b9b;
  border-bottom: #169b9b 3px solid;
  /* 참고 */ }

.branding {
  float: left;
  padding: 20px 0px 20px 80px; }

.boxImg {
  width: 140px;
  height: 50px; }



