@import url(//spoqa.github.io/spoqa-han-sans/css/SpoqaHanSans-kr.css);
@import url(//spoqa.github.io/spoqa-han-sans/css/SpoqaHanSans-jp.css);
/* #f7f7f7; */
body {
  font-family: 15px/1.5 'Spoqa Han Sans',"Roboto","Apple SD Gothic Neo","Malgun Gothic","Arial,sans-serif,serif";
  padding: 0;
  margin: 0;
  background-color: #fff; }

.container {
  width: 80%;
  margin: auto;
  overflow: hidden; }

.ul {
  margin: 0;
  padding: 0; }

.ipad {
  min-Width: 240px;
  height: 300px;
  float: right;
  width: 67%; }
  @media (max-width: 1439px) {
    .ipad {
      width: 67%; } }
  @media (max-width: 1365px) {
    .ipad {
      width: 67%; } }
  @media (max-width: 1023px) {
    .ipad {
      width: 50%; } }
  @media (max-width: 767px) {
    .ipad {
      width: 100%; } }

.Hotline {
  min-width: 260px;
  float: left;
  margin-left: 40px;
  border-right: solid #dfdfdf;
  /*
    h2{
        color : $selected-item;
    }
    */ }
  @media (max-width: 1439px) {
    .Hotline {
      border-right: none; } }
  @media (max-width: 1365px) {
    .Hotline {
      border-right: solid #dfdfdf; } }
  @media (max-width: 1023px) {
    .Hotline {
      border-right: solid #dfdfdf; } }
  @media (max-width: 767px) {
    .Hotline {
      border-right: none; } }
  .Hotline .content {
    margin-top: 30px;
    margin-bottom: 30px;
    font-size: 20px;
    font-weight: bold; }
  .Hotline .title {
    font-size: 17px;
    font-weight: bold;
    color: #0E6EB8; }
  .Hotline ol li {
    font-size: 15px;
    -ms-flex-line-pack: center;
        align-content: center;
    min-width: 160px;
    padding: 0px;
    list-style: circle;
    display: list-item; }
  .Hotline a {
    padding: 10px;
    width: 200px; }
  @media (max-width: 1300px) {
    .Hotline body {
      background: gray; } }

.top {
  color: #169b9b; }

.imgSlide .slider-wrapper {
  overflow: hidden;
  width: 100%;
  position: relative;
  height: 300px; }

.imgSlide .slides {
  -webkit-transition: left 2s;
  -o-transition: left 2s;
  transition: left 2s;
  position: absolute;
  top: 0;
  height: 100%; }
  .imgSlide .slides li {
    float: left;
    background: #ccc;
    list-style-type: none;
    height: 300px; }

.imgSlide .pagers {
  text-align: center;
  top: -50px;
  z-index: 1000;
  position: relative; }
  .imgSlide .pagers li {
    width: 20px;
    height: 20px;
    border: 1px solid #fff;
    display: inline-block;
    border-radius: 20px;
    margin-right: 8px; }



